import { BrowserRouter, Routes, Route } from "react-router-dom";
import Valentine from "./valentine/Valentine";
import AcceptProposal from "./valentine/AcceptProposal";

export default function App() {
  return (
    <BrowserRouter>
      <Routes>
        <Route>
          <Route index element={<Valentine />} />
          <Route path="/success" element={<AcceptProposal />} />
        </Route>
      </Routes>
    </BrowserRouter>
  );
}